.switch {
  width: 40px;
  height: 20px;
  margin: 0 5px;
  display: flex;
  position: relative;
}

.switch_input, .switch_input--mobile {
  height: 0;
  width: 0;
  opacity: 0;
}

.switch_slider {
  cursor: pointer;
  background-color: #fff;
  border: .6px solid #4b48db;
  border-radius: 20px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch_slider:before {
  content: "";
  cursor: pointer;
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1.4px;
  left: 1px;
}

.switch_input--mobile:checked + .switch_slider, .switch_input:checked + .switch_slider {
  background-color: #4440f7;
}

.switch_input--mobile:checked + .switch_slider:before, .switch_input:checked + .switch_slider:before {
  background-color: #fff;
  transform: translateX(21px);
}

